.menu {
	&__content {
		padding-bottom: 60px;
		@include sm {
			padding-bottom: 27px; } } }

.content {
	&__row {
		padding: 70px 0 0 0;
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include md {
			flex-direction: column; }
		@include r(767) {
			display: block;
			padding: 26px 0 0 0; }
		&:nth-child(even) .content__col_sm {
			order: 1;
			padding-right: 0;
			padding-left: 20px;
			@include md {
				order: 0;
				padding-left: 0; } } }
	&__col {
		width: 50%;
		text-align: center;
		@include md {
			width: 100%; }
		&_sm {
			max-width: 531px;
			padding-right: 20px;
			@include md {
				max-width: 100%;
				padding-right: 0;
				padding-left: 0;
				margin-bottom: 30px; }
			@include sm {
				margin-bottom: 20px; } }
		&_or {
			order: 1;
			padding-right: 0;
			padding-left: 20px;
			@include md {
				padding-left: 0;
				order: 0; } } }
	&__subtitle-md {
		margin-bottom: 20px;
		@include sm {
			margin-bottom: 10px; } }
	&__descr {
		color: $gray;
		line-height: 1.88;
		margin-bottom: 20px;
		font-family: $Futuras;
		font-weight: 500;
		font-size: 16px;
		@include sm {
			font-size: 13px; } }
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -10px;
		@include sm {
			margin: 0 -5px; } }
	&__item {
		// width: 46.7%
		// height: 200px
		// padding-bottom: 32.3%
		padding-bottom: 65%;
		position: relative;
		flex: 1 1 50%;
		// margin: 10px
		// &_bigh
		// 	height: 403px
		// 	+sm
		// 		height: 260px
		&_sm {
			padding-bottom: 32.3%; } }
	&__bg {
		position: absolute;
		left: 10px;
		right: 10px;
		top: 10px;
		bottom: 10px;
		background-size: cover;
		background-position: 50% 50%;
		background-repeat: no-repeat;
		@include sm {
			left: 5px;
			top: 5px;
			right: 5px;
			bottom: 5px; } }
	&__link {
		text-decoration: none;
		font-size: 20px;
		font-weight: 500;
		color: $blue; } }

